import React, { Component } from 'react';

import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableBody from "@material-ui/core/TableBody";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";

import {withStyles} from "@material-ui/core/styles";

const styles = theme => ({

});

class MembersComponent extends Component {
  render() {
    const {classes, data} = this.props;

    return (
      <Paper className={classes.root}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell numeric>Trophies</TableCell>
              <TableCell>Rank</TableCell>
              <TableCell>Previous weeks donations</TableCell>
              <TableCell>Current week donations</TableCell>
              <TableCell>War</TableCell>
              <TableCell numeric>Score #1</TableCell>
              <TableCell numeric>Score #2</TableCell>
              <TableCell numeric>Score #3</TableCell>
              <TableCell numeric>Total score</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((m, i) => {
              return (
                <TableRow key={i}>
                  <TableCell>{m.name}</TableCell>
                  <TableCell numeric>{m.trophies}</TableCell>
                  <TableCell>{m.rank}</TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell numeric>{m.weekDonationsScore}</TableCell>
                  <TableCell numeric>{m.dayDonationsScore}</TableCell>
                  <TableCell numeric>{m.warScore}</TableCell>
                  <TableCell numeric>{m.totalScore}</TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </Paper>
    );
  }
}

export default withStyles(styles)(
  MembersComponent
);


